.sideLogo {
    margin-top: 2em;
    margin-bottom: 2em;
    width: 4.5em;
    height: 1em;
}

.singOutIcon {
    width: 20px;
    height: 20px;
    font-size: 1.5rem;
    color: grey;
    position: absolute;
    bottom: 2.5rem;
    left: 2rem;
    text-align: center;
    vertical-align: bottom;
}

.sideHover {
    font-size: 1.5em;
    /* font-family: 'Nunito', sans-serif; */
    color: grey;

    margin-bottom: 0;

    padding: 1rem 1.5rem 1rem 1.5rem;

    text-align: center;
    -webkit-transition: .5s;
    /* ...and now for the proper property */
    transition: .5s;
}

.sideHover:hover {
    background-color: #dddddd;
}
.scrollStuff {
  -ms-overflow-style: none; 
  scrollbar-width: none;  
}

.scrollStuff::-webkit-scrollbar { 
  display: none;  
}
.clientBox { 
  height: 25em;
  width: 18em;
  /* border: 1px solid black; */
  text-align: center;
  margin: 0 0 4em 0;
}

.previewBox {
  height: 1em;
  width: 100%;
  /* border: 1px solid black; */
  text-align: center;
  margin: 0 0 4em 0;
}

.clientColorbox {
  /* background-color: orange; */
  /* color: orange; */
  height: 8em;
  width: 18em;
}

.greenGrad {
  background: -webkit-linear-gradient(bottom, #93f07c 0%, #099214 75%);
  background: linear-gradient(to top, #93f07c 0%, #099214 75%);
}

.redGrad {
  background: -webkit-linear-gradient(bottom, #ff8585 0%, #b41313 75%);
  background: linear-gradient(to top, #ff8585 0%, #b41313 75%);
}

.name {
  padding-top: 1em;
  text-align: center;
  font-size: 1.2em;
}

.clientHR {
  width: 15em;
}

.description {
  margin: 0 1.1em 0 1.1em;
  text-align: center;
  font-size: 1em;
}

.circle {
  border-radius: 50%;
  width: 150px;
  height: 150px; 
  text-align: center;
  display: inline-block;
  margin-top: -5em;
  object-fit: cover;
}

.email {
  float: right;
  margin: 20px 20px 0 0;
  color: whitesmoke;
  font-size: 1.3em;
}

.add {
  float: left;
  margin: 20px 0 0 20px;
  color: whitesmoke;
  font-size: 1.3em;
  cursor: pointer;
}
.ITAtlasText {
  margin-top: 0.7em;
  margin-left: 1.5em;
  font-size: 2em;
  color: #606060;
  font-family: 'Lato', sans-serif;
}

.stateText {
  margin-top: 1em;
  margin-left: 1.5em;
  font-size: 1.5em;
  color: #606060;
  font-family: 'Lato', sans-serif;
}

.blueGrad {
  background: -webkit-linear-gradient(left, #33ccff 0%, #0066cc 83%);
  background: linear-gradient(to right, #33ccff 0%, #0066cc 83%);
}

.shareButton {
  background: -webkit-linear-gradient(left, #33ccff 0%, #0066cc 83%);
  background: linear-gradient(to right, #33ccff 0%, #0066cc 83%);
  height: 46px;
  position: absolute; 
  right: 25px;
}

.gradient {
  background: -webkit-linear-gradient(left, #33ccff 0%, #0066cc 83%);
  background: linear-gradient(to right, #33ccff 0%, #0066cc 83%);
  height: 80px;
  text-align: center;
  font-size: 2em;
  font: black;
  font-family: 'Lato', sans-serif;
}

.userIcons {
  position: absolute; 
  right: 150px;
  margin-top: 14px;
}

.accountdropdown{
  padding-left: 50px;
  padding-top: 15px;
}

.dropdownColor {
  background: -webkit-linear-gradient(left, #ffcc00 0%, #ff9900 50%);
  background: linear-gradient(to right, #ffcc00 0%, #ff9900 50%);
}

.avatar {
  position: absolute;
  right: 175px;
  padding-top: 15px;
}
Body {
  background-color: #f5f5f5 !important;
  overflow-x: hidden;
}


